.HomePage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.HomePage .ContentArea {
    margin: 25px 0;
}

.HomePage .downloadLatestVersion {
    display: flex;
}

@media (max-width: 1000px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        width: 150px !important;
        height: 150px !important;
    }
}

@media (max-width: 600px) {
    .HomePage .downloadLatestVersion .ImageLabel {
        display: none;
    }
    .HomePage .downloadLatestVersion .infoArea {
        margin-left: unset !important;
    }
    .HomePage .downloadLatestVersion .ButtonArea {
        margin: 0 10px 0 0;
    }
    .HomePage .downloadLatestVersion .ButtonArea a {
        width: 100%;
    }
    .HomePage .downloadLatestVersion .ButtonArea .LinkButton {
        margin: 5px 0;
        width: auto;
    }
    .HomePage .downloadLatestVersion .ButtonArea .Button {
        margin: 5px 0;
    }
}

.HomePage .downloadLatestVersion .ButtonArea {
    margin: 20px 0 0 auto;
    width: fit-content;
    gap: unset;
}

.HomePage .downloadLatestVersion .ButtonArea .LinkButton {
    margin: 5px;
    padding: 10px 30px;
    font-size: 28px;
    text-align: center;
    border-radius: 15px;
    transition: 0.3s ease;
}

.HomePage .downloadLatestVersion .ButtonArea .LinkButton:hover {
    transform: translateY(-3px);
}

.HomePage .downloadLatestVersion .ButtonArea .downloadButton {
    display: flex;
    margin: 5px;
    transition: 0.3s ease;
}

.HomePage .downloadLatestVersion .ButtonArea .downloadButton:hover {
    transform: translateY(-3px);
}

.HomePage .downloadLatestVersion .ButtonArea .downloadPartButton {
    display: flex;
    margin: unset;
    padding: 10px 10px 10px 30px;
    font-size: 28px;
    color: #ffffff;
    background-color: #00cf00;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 15px 0 0 15px;
}

.HomePage .downloadLatestVersion .ButtonArea .downloadPartButton:hover {
    background-color: #00b400;
}

.HomePage .downloadLatestVersion .ButtonArea .downloadPartButton:active {
    background-color: #008600;
}

.HomePage .downloadLatestVersion .ButtonArea .releasesPartButton {
    display: flex;
    margin: unset;
    padding: 0 5px;
    height: 100%;
    justify-content: center;
    align-items: center;
    border-radius: 0 15px 15px 0;
}

.HomePage .ButtonArea .releasesDropdown {
    position: absolute;
    top: calc(100% - 5px);
    right: 5px;
    display: block;
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.2) !important;
    border-radius: 10px;
    overflow: hidden;
    z-index: 1;
}

.HomePage .ButtonArea .releasesDropdown .releasesDropdownOption {
    display: flex;
    padding: 10px 20px;
    color: #ffffff;
    background-color: #00cf00;
    text-decoration: none;
    gap: 10px;
    align-items: center;
    font-size: 15pt;
}

.HomePage .ButtonArea .releasesDropdown .releasesDropdownOption:hover {
    background-color: #00b400;
}

.HomePage .ButtonArea .releasesDropdown .releasesDropdownOption:active {
    background-color: #008600;
}

.HomePage .features {
    font-size: 15pt;
    font-family: 'Noto Sans KR', sans-serif !important;
}

.HomePage .faq .ContentSubtitle {
    margin-bottom: 10px;
}

.HomePage .faq .ContentTextArea {
    font-family: 'Noto Sans KR', sans-serif !important;
}